          {
            required: true,
            message: ""
          }
        ],
        foodLicensePic: [
          {
            required: true,
            message: ""
          }
        ],
        muslimfoodPermitPic: [
          {
            required: true,
            message: ""
          }
        ],
        telephone: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        regId: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        legalpepresent: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        selectedOptions2: [
          {
            type: "array",
            required: true,
            message: "",
            // validator: validateChannelCode,
            trigger: "change"
          }
        ],
        remark: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        customerProperty: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        password: [
          {
            required: true,
            validator: validatePass,
            trigger: "blur"
          }
        ],
        repassword: [
          {
            required: true,
            validator: validatePass2,
            trigger: "blur"
          }
        ],
        property: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        enterpriseType: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        defaultProperty: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        isLocal: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        businessScope: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      repassword: "",
      channelList: [],
      timer: null,
      customerTypeList: [],
      categoryTypeList: [],
      TCTypeList: []
    };
  },
  created() {
    /**
     * 
     */
    this.postRequest("channel/list", {
      level: 3,
      deleted: 0,
      isDefault: 1
    }).then(d => {
      if (d.data && d.data.state == 0) {
        if (d.data.aaData.length > 0) {
          this.defaultChannelCode = d.data.aaData[0].code;
        }
      }
    });
    this.postRequest("channel/listByParent", {
      parentChannelCode: "001"
    }).then(d => {
      if (d.data && d.data.state == 0) {
        this.ChannelOption = d.data.aaData;
        let options = [];
        for (let item of d.data.aaData) {
          let lv2Obj = {
            value: item.code,
            label: item.name,
            children: []
          };
          for (let lv3item of item.childChannel) {
            let lv3Obj = {
              value: lv3item.code,
              label: lv3item.name
            };
            lv2Obj.children.push(lv3Obj);
          }
          options.push(lv2Obj);
        }
        options.push({
          value: "QT",
          label: ""
        });
        this.options = options;
        // console.log(options)
        // this.getChannelOptions()
      }
    });
    // 
    if (this.$route.query.regStep) {
      this.active = Number(this.$route.query.regStep);
    }
    // 
    if (sessionStorage.getItem("jz_saveInputInfo")) {
      this.form = JSON.parse(sessionStorage.getItem("jz_saveInputInfo"));
    }
    this.getTCType();
    this.getCustomerType();
    this.getChannel();
    this.getCategoryType();
    // 
    if (sessionStorage.getItem("jz_getAddrByMap")) {
      var getAddrByMap = JSON.parse(sessionStorage.getItem("jz_getAddrByMap"));
      this.userSelectAddressInfo = getAddrByMap;
      this.selectedAddressStr = `${getAddrByMap.pname}${getAddrByMap.cityname}${
        getAddrByMap.adname
      }${getAddrByMap.address}`;
      if (
        this.form.customerProperty == "004" ||
        this.form.customerProperty == "005"
      ) {
        this.form.address = this.selectedAddressStr;
        // 
        this.address_param = getAddrByMap.address;
      }
    }
    // 
    if (this.$store.state.temporarUserId) {
      this.isUpdate = true;
      this.postRequest("customer/queryById", {
        id: this.$store.state.temporarUserId
      }).then(resp => {
        if (resp.data && resp.data.state == 0) {
          if (resp.data.aaData) {
            this.form.name = resp.data.aaData.name;
            this.form.telephone = resp.data.aaData.tel;
          }
        }
      });
    } else {
      this.isUpdate = false;
    }
  },
  methods: {
    /**
     * @description:
     * @param {type}
     * @return:
     */
    getChannelOptions(arr, childs) {
      for (let item of arr) {
        console.log(item);
        if (childs > 0) {
          this.getChannelOptions(arr, childs);
        }
      }
    },
    handleChange(value) {
      if (value[1]) {
        this.form.channelCode = value[1];
        console.log(this.form.channelCode);
      } else {
        this.form.channelCode = "QT";
      }
    },
    goback() {
      clearInterval(this.timer);
      if (this.$route.query.type === "merchant") {
        window.history.back(-1);
        return false;
      }
      this.$router.push({
        name: "login"
      });
    },
    closeUploadImg(img) {
      this.form[img] = "";
    },
    /**
     * 
     */
    checkIdentity(customerProperty) {
      switch (customerProperty) {
        case "001":
          this.regTypeText = "";
          break;
        case "002":
          this.regTypeText = "//";
          break;
        case "003":
          this.regTypeText = "/";
          break;
        case "004":
          this.regTypeText = "";
          break;
        case "005":
          this.regTypeText = "";
          break;
        case "007":
          this.regTypeText = "";
          break;
        case "008":
          this.regTypeText = "";
          break;
        default:
          this.regTypeText = "";
          break;
      }
      this.form.customerProperty = customerProperty;

      this.active++;
    },
    /**
     * 
     */
    pre() {
      this.active--;
      this.form.property = "";
      sessionStorage.removeItem("jz_saveInputInfo");
      sessionStorage.removeItem("jz_getAddrByMap");
    },
    /**
     * @description: 
     * @param {type}
     * @return:
     */
    submitUpdate() {
      this.$refs["form"].validate(async valid => {
        if (valid) {
          this.$refs["form1"].validate(valid1 => {
            if (valid1) {
              // 
              if (
                this.form.customerProperty == "004" ||
                this.form.customerProperty == "005"
              ) {
                this.updateRegPost(true);
              } else {
                this.updateRegPost();
              }
            } else {
              return false;
            }
          });
        } else {
          return false;
        }
      });
    },
    /**
     * 
     */
    saveReg() {
      this.$refs["form"].validate(async valid => {
        if (valid) {
          // let resMatche = await this.telephoneMatche();
          // if (resMatche.data && resMatche.data.state == 1) {
          this.$refs["form1"].validate(valid1 => {
            if (valid1) {
              // 
              if (
                this.form.customerProperty == "004" ||
                this.form.customerProperty == "005"
              ) {
                this.regPost(true);
              } else {
                this.regPost();
              }
            } else {
              return false;
            }
          });
          // } else {
          //   this.$message.error(`${resMatche.data.msg}!`);
          // }
        } else {
          return false;
        }
      });
    },
    /**
     * 
     * @param 
     */
    regPost(isgrOrMd) {
      this.postRequest("customer/customerRegist", this.checkParam()).then(d => {
        if (d.data && d.data.state == 0) {
          // 
          if (isgrOrMd) {
            if (this.userSelectAddressInfo) {
              this.createUserAddress(d.data.aaData.id);
            }
          } else {
            this.active++;
            this.timer = setInterval(() => {
              this.second--;
              if (this.second == 0) {
                clearInterval(this.timer);
                this.goback();
              }
            }, 1000);
          }
        }
      });
    },
    /**
     * @description: 
     * @param {type} isgrOrMd
     * @return:
     */
    updateRegPost(isgrOrMd) {
      let param = this.checkParam();
      param.id = this.$store.state.temporarUserId;
      param.extend = this.form.customerProperty;
      this.postRequest("customer/update", param).then(d => {
        if (d.data && d.data.state == 0) {
          this.$store.state.temporarUserId = null;
          // 
          if (isgrOrMd) {
            if (this.userSelectAddressInfo) {
              this.createUserAddress(this.$store.state.temporarUserId);
            }
          } else {
            this.active++;
            this.timer = setInterval(() => {
              this.second--;
              if (this.second == 0) {
                clearInterval(this.timer);
                this.goback();
              }
            }, 1000);
          }
        }
      });
    },
    searchLocal(val) {
      this.locationVal.search(val);
    },
    /**
     * @description: 
     * @param {type}
     * @return:
     */
    checkParam() {
      let param = {
        isGetId: 1,
        name: this.form.name,
        telephone: this.form.telephone,
        password: this.form.password,
        customerProperty: this.form.customerProperty,
        regId: this.form.regId,
        businessLicensePic: this.form.businessLicensePic,
        marketcertPic: this.form.marketcertPic,
        legalpepresent: this.form.legalpepresent,
        legalpepresentPic: this.form.legalpepresentPic,
        foodLicensePic: this.form.foodLicensePic,
        muslimfoodPermitPic: this.form.muslimfoodPermitPic,
        storePic: this.form.storePic,
        address: this.form.address,
        type: this.form.type,
        enterpriseType: this.form.enterpriseType,
        marketcertPic: this.form.marketcertPic
      };
      if (this.form.idCardPic_01 != "" && this.form.idCardPic_02 != "") {
        param.idCard = `${this.form.idCardPic_01};${this.form.idCardPic_02}`;
      }

      if (param.customerProperty == "007" || param.customerProperty == "001") {
        this.form.businessScope
          ? (param.businessScope = this.form.businessScope.join("/"))
          : (param.businessScope = "");
      }
      // 4,
      if (param.customerProperty == "001" || param.customerProperty == "002") {
        if (this.form.channelCode == "QT") {
          param.channelCode = "";
          param.remark = this.form.remark;
        } else {
          param.channelCode = this.form.channelCode;
          param.remark = "";
        }
      } else {
        param.channelCode = "";
        param.remark = "";
      }
      // 1p
      if (param.customerProperty == "001") {
        param.islocal = this.form.isLocal;
      }
      // 1,2p
      if (param.customerProperty == "001" || param.customerProperty == "002") {
        param.property = this.form.property;
      } else if (param.customerProperty != "005") {
        param.property = this.defaultProperty;
      }
      // 
      if (param.channelCode == "" && param.remark == "") {
        param.channelCode = this.defaultChannelCode;
      }

      return param;
    },
    /**
     * 
     */
    createUserAddress(customerId) {
      var url = "address/create";
      var param = {
        provinceCode: this.userSelectAddressInfo.pcode,
        cityCode: this.userSelectAddressInfo.cityCode,
        districCode: this.userSelectAddressInfo.adcode,
        detailedAddress: this.address_param,
        contacts: this.form.name,
        phoneNumber: this.form.telephone,
        customerId: customerId,
        comment: "",
        latitude: this.userSelectAddressInfo.location.lat,
        longitude: this.userSelectAddressInfo.location.lng
      };
      // cityCode
      this.postRequest("comArea/list", {
        parentAreaCode: param.provinceCode,
        level: 2
      }).then(d => {
        if (d.data && d.data.state == 0) {
          for (let a = 0; a < d.data.aaData.length; a++) {
            if (
              d.data.aaData[a].areaName == this.userSelectAddressInfo.cityname
            ) {
              param.cityCode = d.data.aaData[a].areaCode;
              break;
            }
          }
          // 
          this.postRequest(url, param).then(d => {
            if (d.data && d.data.state == 0) {
              this.active++;
              this.timer = setInterval(() => {
                this.second--;
                if (this.second == 0) {
                  clearInterval(this.timer);
                  this.goback();
                }
              }, 1000);
              sessionStorage.removeItem("jz_getAddrByMap");
              sessionStorage.removeItem("jz_saveInputInfo");
            }
          });
        }
      });
    },
    /**
     * 
     */
    marketcertPicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.businessLicensePic = URL.createObjectURL(file.raw);
        this.form.marketcertPic = res.aaData.path;
      }
    },
    /**
     * 
     */
    businessLicensePicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.businessLicensePic = URL.createObjectURL(file.raw);
        this.form.businessLicensePic = res.aaData.path;
      }
    },
    /**
     * 
     */
    foodLicensePicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.foodLicensePic = URL.createObjectURL(file.raw);
        this.form.foodLicensePic = res.aaData.path;
      }
    },
    /**
     * 
     */
    muslimfoodPermitPicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.muslimfoodPermitPic = URL.createObjectURL(file.raw);
        this.form.muslimfoodPermitPic = res.aaData.path;
      }
    },
    /**
     * 
     */
    legalpepresentPicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.legalpepresentPic = URL.createObjectURL(file.raw);
        this.form.legalpepresentPic = res.aaData.path;
      }
    },
    /**
     * 
     */
    idCardPicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.idCardPic_01 = URL.createObjectURL(file.raw);
        this.form.idCardPic_01 = res.aaData.path;
      }
    },
    /**
     * 
     */
    idCardPicSuccess1(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.idCardPic_02 = URL.createObjectURL(file.raw);
        this.form.idCardPic_02 = res.aaData.path;
      }
    },
    /**
     * 
     */
    storePicSuccess(res, file) {
      if (res.state == 0 && res.aaData) {
        // this.storePic = URL.createObjectURL(file.raw);
        this.form.storePic = res.aaData.path;
      }
    },
    /**
     * 
     */
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      //         if (!isJPG) {
      //           this.$message.error(' JPG !');
      //         }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      // return isJPG && isLt2M;
    },
    /**
     * 
     */
    // getCustomerType() {
    //   this.postRequest("datadic/list", {
    //     dataType: "CUSTOMER_TYPE",
    //     extendVal: "merchant"
    //   }).then(d => {
    //     if (d.data && d.data.state == 0) {
    //       if (d.data.aaData) {
    //         this.customerTypeList = d.data.aaData;
    //       }
    //     }
    //   });
    // },
    /**
     * 
     */
    getCustomerType() {
      this.postRequest("datadic/list", {
        dataType: "CUSTOMER_PROPERTY"
        // extendVal: "merchant"
      }).then(d => {
        if (d.data && d.data.state == 0) {
          if (d.data.aaData) {
            this.customerTypeList = d.data.aaData;
          }
        }
      });
    },
    getTCType() {
      this.postRequest("datadic/list", {
        dataType: "TC_TYPE"
      }).then(d => {
        if (d.data && d.data.state == 0) {
          if (d.data.aaData) {
            this.TCTypeList = d.data.aaData;
          }
        }
      });
    },
    /**
     * 
     */
    getChannel() {
      //
      this.postRequest("channel/list", {
        level: 3,
        enabled: 1,
        deleted: 0
      }).then(d => {
        if (d.data && d.data.state == 0) {
          if (d.data.aaData) {
            d.data.aaData.push({
              code: "QT",
              name: ""
            });
            this.channelList = d.data.aaData;
          }
        }
      });
    },
    /**
     * 
     */
    getCategoryType() {
      this.postRequest("category/list", {
        level: 1,
        enabled: 1
      }).then(d => {
        if (d.data && d.data.state == 0) {
          if (d.data.aaData) {
            this.categoryTypeList = d.data.aaData;
          }
        }
      });
    },
    /**
     * 
     */
    telephoneMatche() {
      return new Promise((resolve, reject) => {
        this.postRequest("customer/telephoneMatche", {
          telephone: this.form.telephone
        }).then(d => {
          resolve(d);
        });
      });
    },
    /**
     * 
     */
    openMap(d, type) {
      window.sessionStorage.setItem(
        "jz_saveInputInfo",
        JSON.stringify(this.form)
      );
      this.$router.push({
        name: "selectAddrByMap"
      });
    }
  }
};
</script>
<style lang="scss">
#regPage {
  .uploadimgPre {
    width: 121px;
    height: 121px;
    border-radius: 3px;
    margin: 10px 10px 10px 136px;
    position: relative;
    .el-icon-error {
      position: absolute;
      right: -8px;
      color: red;
      font-size: 16px;
      top: -8px;
      cursor: pointer;
    }
    img {
      width: 100%;
      height: 100%;
    }
  }

  .el-button--danger {
    background-color: rgb(246, 72, 17);
    border-color: rgb(246, 72, 17);
  }

  .el-step__head.is-finish {
    color: #ff4400;
    border-color: #ff4400;

    .el-step__icon.is-text {
      background: #ff4400;
      color: #fff;
    }
  }

  .el-step__title.is-finish {
    color: #ff4400;
  }

  .el-radio__input.is-checked .el-radio__inner {
    border-color: #ff4400;
    background: #ff4400;
  }

  .el-radio__input.is-checked + .el-radio__label {
    color: #ff4400;
  }

  .shop-search {
    height: 100px;
    box-shadow: 0 0 10px #e6e9e3;

    .logo-l {
      width: 236px;
      height: 63px;
      background: url(../../static/img/northring/logo_new.png);
      margin-right: 10px;
    }

    .click-a {
      color: #f64811;
      margin-left: 10px;
    }
  }

  .formUserInfo {
    .el-form-item__label {
      width: 132px !important;
    }
  }

  .form-container {
    .width578 {
      width: 578px;
      margin: 0 auto;
    }

    .el-form-item__label {
      width: 90px;
      text-align: left;
      padding: 0 12px 0 !important;
    }

    .el-form-item__content {
      border: 1px solid #bdbdbd;
    }

    width: 1024px;
    margin: 0 auto;

    .step {
      padding: 90px 0 30px;
    }

    .formlist {
      .form-group {
        .user-type {
          p {
            padding: 4px 0;
          }
        }

        span.name {
          width: 120px;
          display: block;
          height: 40px;
          line-height: 40px;
          padding-left: 16px;
        }

        .el-input__inner {
          border: none;
          font-size: 16px;
        }
      }

      .regsuc {
        border-bottom: 1px solid #d9d9d9;
        padding-bottom: 30px;

        .text {
          font-size: 36px;
          color: #f64811;
          margin-left: 30px;
        }
      }

      p.tips {
        padding-top: 55px;
        text-align: center;

        span.click-a {
          color: #1e88e5;
        }
      }
    }
  }

  footer {
    .min-width {
      border-top: 1px solid #e5e5e5;
      padding: 33px 0 100px;
      margin-top: 74px;
      text-align: center;

      p {
        font-size: 14px;
        color: #666;
      }
    }
  }

  // 
  p.identity-title {
    color: #999;
    margin-bottom: 20px;
  }

  ul.identity-list {
    li {
      flex-wrap: wrap;

      .item {
        cursor: pointer;
        width: 268px;
        height: 58px;
        border: 1px solid #bdbdbd;
        margin-bottom: 20px;

        img {
          margin-right: 12px;
        }

        p {
          color: #666;
        }
      }
    }

    .item:last-child {
      width: 100%;
      height: 58px;
    }
  }

  // 
  .user_info {
    width: 1024px;
    margin: 0 auto;

    .user_info_fengexin {
      width: 1px;
      height: 300px;
      background: #bdbdbd;
    }

    .user_base_info,
    .user_com_info {
      width: 398px;
    }

    .user_base_info {
      .el-form-item__label {
        width: 122px !important;
      }
    }
  }
}
</style>



